.footer {
    padding: 30px 0;
    background-color: black;
    color: #fff;
  }
  .grid-footer {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .copyright-name,
  .copyright-para {
    margin-bottom: 10px;
    color: rgba(255, 255, 255, 0.7);
    font-family: "Roboto", sans-serif;
    font-size: 1rem;
  }
  .footer-img {
    width: 100px;
    justify-self: end;
  }
  
  @media(min-width:1024px){
    .footer-img{
      width:200px;
    }
  }