.v-carousel-item {
    height: 70vh;
    width: 100%;
    margin: 0;
    position: relative;
  }
  .v-carousel-item figcaption {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: #fff;
  }
  
  .slick-dots li.slick-active button:before {
    color: white !important;
  }
  .slick-dots li button:before {
    font-size: 15px;
  }
  
  .slick-dots {
    bottom: 25px;
  }
  .desc_now_playing {
    position: absolute;
    bottom: 20%;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
  }
  .movie {
    color: white;
    font-family: "Roboto", sans-serif;
    font-size: 36px;
    text-align: left;
  }
  v-img {
    width: 100%;
  }
  .carousel-title-now-playing {
    color: black;
    font-family: "Alegreya Sans", sans-serif;
    font-size: 1.5rem;
    font-weight: bold;
    letter-spacing: 0.2rem;
    padding: 10px 0;
    text-transform: uppercase;
  }
  .genre {
    padding: 4px 16px;
    background: #ff2a55;
    border-radius: 50px;
    margin: 8px 10px 8px 0;
    display: inline-block;
    font-size: 14px;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.4);
  }
  